import { Component } from './base/component';
import { ensureElement } from '../utils/utils';
import { eventList, settings } from '../utils/constants';
const selector = settings.modalTemplateSelectors;
export class Modal extends Component {
    constructor(container, events) {
        super(container);
        this.events = events;
        this.opening = false;
        //Обработчик нажатия клавиши Esc
        this.escHandle = (event) => {
            if (event.key === "Escape")
                this.close();
        };
        this._closeButton = ensureElement(selector.closeBtn, container);
        this._content = ensureElement(selector.content, this.container);
        this._closeButton.addEventListener('click', () => this.close.bind(this));
        this.container.addEventListener('click', this.close.bind(this));
        this._content.addEventListener('click', (event) => event.stopPropagation());
    }
    set content(value) {
        this._content.replaceChildren(value);
    }
    open() {
        this.opening = true;
        this.toggleClass(this.container, selector.activeModal);
        this.events.emit(eventList.MODAL_OPEN_CLOSE);
        //Добавляем слушателя клавишь
        document.addEventListener('keydown', this.escHandle);
    }
    close() {
        this.opening = false;
        this.toggleClass(this.container, selector.activeModal);
        this.content = null;
        this.events.emit(eventList.MODAL_OPEN_CLOSE);
        //Удаляем слушателя клавиш
        document.removeEventListener('keydown', this.escHandle);
    }
    render(data) {
        if (this.opening)
            this.close();
        super.render(data);
        this.open();
        return this.container;
    }
}
